<template>
  <div>
    <!-- 載入畫面元件 -->
    <loading :active.sync="isLoading"></loading>

    <!-- 訂單資訊列表 -->
    <div class="my-5 row justify-content-center" v-if="order.id">  <!-- 檢查是否有訂單 -->
      <form class="col-md-6">
        <table class="table">
          <thead>
            <th>品名</th>
            <th>數量</th>
            <th>單價</th>
          </thead>
          <tbody>
            <tr v-for="item in order.products" :key="item.id">
              <td class="align-middle">{{ item.product.title }}</td>
              <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
              <td class="align-middle text-right">{{ item.final_total }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="2" class="text-right">總計</td>
              <td class="text-right">{{ order.total }}</td>
            </tr>
          </tfoot>
        </table>

        <table class="table">
          <tbody>
            <tr>
              <th width="100">Email</th>
              <td>{{ order.user.email }}</td>
            </tr>
            <tr>
              <th>姓名</th>
              <td>{{ order.user.name }}</td>
            </tr>
            <tr>
              <th>收件人電話</th>
              <td>{{ order.user.tel }}</td>
            </tr>
            <tr>
              <th>收件人地址</th>
              <td>{{ order.user.address }}</td>
            </tr>
            <tr>
              <th>付款狀態</th>
              <td>
                <span v-if="!order.is_paid">尚未付款</span>
                <span v-else class="text-success">付款完成</span>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="text-right" v-if="order.is_paid === false">
          <button class="btn btn-danger" @click="payOrder(order.id)">確認付款去</button>
        </div>
      </form>
    </div>

    <div class="alert alert-warning text-center m-5" role="alert" v-if="showMsg">
      沒有此筆訂單！
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isLoading: false,
      order: {
        user: {},
      },
      showMsg: false,
    }
  },
  methods:{
    // 「取得某筆訂單」
    getOrder() {
      let vm = this;
      this.isLoading = true;
      let order_id = vm.$route.params.order_id;
      console.log(order_id);
      const api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/order/${order_id}`;
      this.$http.get(api).then(response => {
        console.log(response.data);
        // 用回傳的order判斷是否有回傳訂單資料，若有再執行以下  (回傳的success都是true)
        if (response.data.order){
          vm.order = response.data.order;
        }else{
          vm.showMsg = true;
        }
        vm.isLoading = false;
      });
    },
    // 「付款結帳」
    payOrder(id) {
      let vm = this;
      this.isLoading = true;
      const api = `${process.env.APISERVER}/api/${process.env.CUSTOMPATH}/pay/${id}`;
      this.$http.post(api).then(response => {
        console.log(response.data);
        if (response.data.success){
          vm.getOrder();
          vm.isLoading = false;
        }
      });
    }
  },
  created() {
    this.getOrder();
  }
}
</script>


